<template>
  <!--  -->
  <div class="admin-home">
    <vchart-line :data="chartData"></vchart-line>
  </div>
</template>
<script>
// v-chart
import VchartLine from 'v-charts/lib/line.common'
import { getTimeArticles } from '@/api/getData'
import { formatTime } from '@/utils/tool'
export default {
  data () {
    return {
      chartData: {
        columns: ['', ''],
        rows: [
          { : '2019-01-15', : 1393 },
          { : '2019-01-16', : 3530 },
          { : '2019-01-17', : 2923 },
          { : '2019-01-18', : 1723 },
          { : '2019-01-19', : 3792 },
          { : '2019-01-20', : 4593 }
        ]
      }
    }
  },
  created () {
    this.getArticlesData()
  },
  components: {
    VchartLine
  },
  methods: {
    async getArticlesData () {
      const { data: { articles } } = await getTimeArticles()
      console.log(articles)
      const datas = articles.map(item => {
        return {
          '': formatTime(item.pubTime),
          '': parseInt(item.scan)
        }
      })
      console.log(datas)
      this.chartData.rows = datas
    }
  }
}
</script>

<style lang="less" scoped>
.admin-home {
  .ve-line{
    padding: 0 20px;
    box-sizing: border-box;
    margin-top: 100px;
  }
}
</style>



